import './../styles/Navigation.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

function Navigation({ page, setPage}){

    return (
        <div className="divNavigation">
            <div className="logo">
                <button onClick={() => setPage("accueil")}>
                    <FontAwesomeIcon icon="robot" size="lg"/>
                    <h1>Olympia</h1>
                </button> 
            </div>
            <div className="menu">
                <ul>
                    <li>    
                        <button onClick={() => setPage("accueil")}>
                            <FontAwesomeIcon icon="home" size="lg" />
                            <h2>Accueil</h2>
                        </button> 
                    </li>
                    <li>
                        <button onClick={() => setPage("seance")}>
                            <FontAwesomeIcon icon="dumbbell" size="lg" />
                            <h2>SÃ©ance</h2>
                        </button> 
                    </li>
                    <li>
                        <button onClick={() => setPage("tableau")}>
                            <i class="fas fa-chart-line"></i>
                            <FontAwesomeIcon icon="chart-line" size="lg" />
                            <h2>Tableau de Bord</h2>
                        </button> 
                    </li>
                </ul>
            </div>
        </div>
    )
}


export default Navigation